* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Cera Round Pro", "Roboto", sans-serif;
  color: $text-color;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

input:not([type="range"]),
select,
textarea {
  border: none;
  outline: none;
  transition: 0.3s ease;
  &:focus {
    box-shadow: 0 0 0 3px $color1;
  }
}

.overflow-hidden {
  overflow: hidden;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 15px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: cover;
  }
}

.bordered-text {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: $color3;
}

.section-sec-title {
  font-size: 48px;
  text-align: center;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.section-title {
  text-transform: uppercase;
  font-size: 120px;
  text-align: center;
}

.circle {
  position: absolute;
  width: 195px;
  height: 195px;
  background: #ed0677;
  border-radius: 50%;
  z-index: 0;
}

.blured {
  filter: blur(35px);
}

.scroll-down {
  position: absolute;
}

.triangle-box {
  width: 46px;
  height: 46px;
  display: block;
  position: absolute;
  background-image: url("../img/icons/triangle.svg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: -1;
}

.flower-box {
  width: 121px;
  height: 121px;
  display: block;
  position: absolute;
  background-image: url("../img/icons/flower.svg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: -1;
}
