.preloader {
  --duration: 1s;
  position: fixed;
  z-index: 2000;
  background: #111;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: var(--duration);
}

.preloader__content {
  position: relative;
  width: 200px;
  height: 200px;
}

.preloader__first,
.preloader__second,
.preloader__third {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  animation: var(--duration) infinite linear;
}

.preloader__first {
  border-bottom: 3px solid #042a8c;
  animation-name: first;
}

.preloader__second {
  border-right: 3px solid #ed0677;
  animation-name: second;
}

.preloader__third {
  border-top: 3px solid #14b1e7;
  animation-name: third;
}

@keyframes first {
  0% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
  }
}

@keyframes second {
  0% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
  }
}

@keyframes third {
  0% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
  }
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Cera Round Pro", "Roboto", sans-serif;
  color: #4b4b4b;
  overflow-x: hidden;
}

button {
  cursor: pointer;
  padding: 0;
  border: none;
  background: transparent;
  outline: none;
}

input:not([type="range"]),
select,
textarea {
  border: none;
  outline: none;
  transition: 0.3s ease;
}

input:not([type="range"]):focus,
select:focus,
textarea:focus {
  box-shadow: 0 0 0 3px #042a8c;
}

.overflow-hidden {
  overflow: hidden;
}

.container {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 15px;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  outline: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

img {
  display: block;
  max-width: 100%;
}

.basic-flex {
  display: flex;
  align-items: center;
}

.img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.img-wrapper img {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}

.bordered-text {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: #14b1e7;
}

.section-sec-title {
  font-size: 48px;
  text-align: center;
  margin-bottom: 30px;
  text-transform: uppercase;
}

.section-title {
  text-transform: uppercase;
  font-size: 120px;
  text-align: center;
}

.circle {
  position: absolute;
  width: 195px;
  height: 195px;
  background: #ed0677;
  border-radius: 50%;
  z-index: 0;
}

.blured {
  filter: blur(35px);
}

.scroll-down {
  position: absolute;
}

.triangle-box {
  width: 46px;
  height: 46px;
  display: block;
  position: absolute;
  background-image: url("../img/icons/triangle.svg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: -1;
}

.flower-box {
  width: 121px;
  height: 121px;
  display: block;
  position: absolute;
  background-image: url("../img/icons/flower.svg");
  background-size: contain;
  background-position: center center;
  background-repeat: no-repeat;
  z-index: -1;
}

.fullpage-menu {
  position: absolute;
  width: 100%;
  height: 100vh;
  top: 0;
  right: 0;
  transform: translateY(-150%);
  padding: 45px 40px;
  background-color: #042a8c;
  overflow: hidden;
  z-index: 10;
  opacity: 1;
  pointer-events: none;
  transition: 0.5s ease;
  background-image: url("../img/menu-bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top right;
}

.fullpage-menu.active {
  pointer-events: unset;
  transform: translateY(0);
}

.fullpage-menu .bottom-img {
  position: absolute;
  bottom: 0;
  right: 0;
}

.fullpage-menu .section-top {
  display: flex;
}

.fullpage-menu .section-top .language-box,
.fullpage-menu .section-top .menu-toggler-btn {
  margin: 0;
  margin-right: 20px;
}

.fullpage-menu .big-text {
  position: absolute;
  top: 50%;
  left: 120px;
  font-size: 350px;
  font-weight: bold;
  color: #193fa0;
  z-index: -1;
  text-transform: uppercase;
  user-select: none;
  pointer-events: none;
}

.fullpage-menu .menu-toggler-btn {
  display: flex;
  align-items: center;
}

.fullpage-menu .menu-toggler-btn .img-wrapper {
  margin-right: 15px;
}

.fullpage-menu .menu-toggler-btn span {
  color: #fff;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 900;
}

.main-menu {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.5s ease;
}

.main-menu .main-menu__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-menu .main-menu__inner:not(:last-child) {
  margin-bottom: 100px;
}

.main-menu li {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-menu li a {
  white-space: nowrap;
  font-size: 36px;
  color: #fff;
  text-transform: uppercase;
  transition: 0.3s ease;
}

.main-menu li a:hover,
.main-menu li a:focus {
  color: #ed0677;
}

.main-menu li::after {
  display: block;
  content: "";
  margin: 0 40px;
  width: 140px;
  height: 3px;
  background-color: #14b1e7;
  transform: rotate(-45deg);
}

.main-menu li:nth-child(4)::after {
  display: none;
}

.main-menu li:last-child {
  margin-right: 0;
}

.main-menu li:last-child::after {
  display: none;
}

.main-menu li.active a {
  color: #ed0677;
}

.main-header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: flex;
  align-items: flex-start;
  background-image: url("../img/header-bg1.svg");
  background-position: top right;
  background-size: contain;
  background-repeat: no-repeat;
  padding-bottom: 100px;
}

.logo-wrapper {
  padding-top: 45px;
  height: 70vh;
  min-height: 500px;
  width: 140px;
  border-radius: 0px 0px 50px 0px;
  background-color: #14b1e7;
}

.logo-wrapper .logo {
  margin-bottom: 200px;
}

.social-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.social-links__item {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.social-links__item:not(:last-child) {
  margin-bottom: 13px;
}

.social-links__item a {
  display: block;
  width: 43px;
  height: 43px;
  line-height: 43px;
  border-radius: 50%;
  font-size: 13px;
  font-weight: 900;
  color: #fff;
  border: 1px solid #fff;
}

.language-box {
  margin-top: 45px;
  width: 88px;
  height: 45px;
  background: #efefef;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 36px;
}

.language-box a {
  width: 38px;
  height: 38px;
  background: #fff;
  line-height: 39px;
  display: block;
  font-size: 13px;
  font-weight: bold;
  color: #d4d4d4;
  border-radius: 50%;
  text-align: center;
}

.language-box a:not(:last-child) {
  margin-right: 4px;
}

.language-box a.active {
  color: #fff;
  background-color: #14b1e7;
}

.menu-toggler-btn {
  display: flex;
  margin-top: 50px;
  margin-left: auto;
  margin-right: 150px;
}

.menu-toggler-btn .img-wrapper {
  margin-right: 10px;
}

.menu-toggler-btn span {
  color: #4b4b4b;
}

.main-footer {
  padding-top: 50px;
  padding-bottom: 40px;
  background-color: #042a8c;
}

.footer-inner > .img-wrapper {
  margin-bottom: 50px;
}

.footer-nav-wrapper {
  justify-content: center;
  margin-bottom: 50px;
}

.footer-nav-wrapper li {
  margin: 0 25px;
}

.footer-nav-wrapper a {
  color: #fff;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
}

.footer-bottom {
  padding-top: 35px;
  border-top: 2px solid #fff;
  justify-content: space-between;
}

.footer-bottom .social-link {
  display: flex;
}

.footer-bottom .social-link li {
  margin-bottom: 0;
  margin-right: 15px;
}

.about {
  padding-top: 200px;
}

.about-inner {
  max-width: 1600px;
  margin: 0 auto;
  padding: 0 15px;
}

.about-top {
  background-image: url("../img/circles.svg");
  background-size: 80%;
  background-position: center bottom;
  background-repeat: no-repeat;
  margin-bottom: 150px;
}

.about-content {
  max-width: 1320px;
  margin: 0 auto;
  position: relative;
}

.about-content .scroll-down {
  left: 55%;
  bottom: 0;
  transform: translateY(70%);
}

.info-yearbox {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 150px;
}

.info-content {
  width: 50%;
}

.info-content p {
  font-size: 48px;
  position: relative;
  z-index: 1;
  font-weight: bold;
  text-transform: uppercase;
}

.info-content .circle {
  transform: translateY(-70%) translateX(100%);
}

.year-box .img-wrapper {
  margin-top: -10px;
  margin-bottom: 15px;
}

.year-box span {
  color: #14b1e7;
  display: block;
  text-align: right;
  font-size: 22px;
}

.year-box span:nth-of-type(1) {
  font-size: 64px;
  margin-bottom: -25px;
}

.with-img {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 200px;
}

.with-img .triangle-box {
  top: 0;
  left: 48%;
}

.with-img .flower-box {
  right: -7%;
  top: 50%;
}

.with-img .bg-img {
  height: 850px;
  border-radius: 50px 0px;
  overflow: hidden;
}

.with-img .bg-img img {
  width: 100%;
  max-height: 100%;
}

.with-img .img-side {
  width: 47%;
  position: relative;
}

.with-img .hero-img {
  top: 30px;
  transform: translateX(16%);
  position: absolute;
}

.with-img .text-side {
  width: 50%;
}

.with-img .text-side p {
  font-size: 18px;
  margin-bottom: 30px;
  line-height: 1.9;
  font-weight: 400;
}

@media (max-width: 1700px) {
  .fullpage-menu {
    width: 100%;
  }

  .main-menu .main-menu__inner {
    flex-direction: row;
    align-items: center;
  }

  .main-menu li a {
    font-size: 28px;
  }

  .main-menu li::after {
    width: 80px;
    height: 2px;
  }
}

@media (max-width: 968px) {
  .fullpage-menu {
    padding: 25px;
    background-image: none;
    background-color: #042a8c;
  }

  .fullpage-menu .big-text {
    display: none;
  }

  .main-menu {
    position: unset;
    transform: none;
    margin-top: 10vh;
  }

  .main-menu li {
    margin-bottom: 30px;
  }

  .main-menu li a {
    font-size: 20px;
  }

  .main-menu li::after {
    display: none;
  }

  .main-menu .main-menu__inner {
    margin-bottom: 0 !important;
    flex-direction: column;
  }
}